.header-banner{
  @include center;
  padding: 1em;
  background: #0A0909;
  color: #fff;
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
  @media screen and (min-width: 993px){
    position: relative;
    z-index: 4;
  }
  &__text{
    line-height: 160%;
  }
}

.header{
  position: relative;
  z-index: 20;
  @media screen and (min-width: 993px) {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #fff;
      z-index: 3;
    }
  }
  &__inner{
    padding: 2.28em 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__logo{
    position: relative;
    z-index: 4;
    display: block;
    width: 3.125em;
    flex-shrink: 0;
    margin: 0 7em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu{
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.menu,
.header-action{
  width: calc(50% - 8.5625em);
  flex-shrink: 0;
}

.header-action__cart span,
.header-lk__link span,
.catalog-btn span,
.nav .menu-item a{
  font-size: 0.6875em;
  line-height: 182%;
  text-transform: uppercase;
}

.nav{
  @media screen and (min-width: 993px){
    position: relative;
    z-index: 4;
  }
  ul{
    display: flex;
    align-items: center;
  }
  .menu-item{
    @include lastM(0 2.5em 0 0);
  }
}

.catalog-btn{
  position: relative;
  z-index: 4;
  display: flex;
  align-items: center;
  margin-right: 2.5em;
  svg{
    width: .5625em;
    margin-left: .5em;
  }
  &:hover{
    color: $red;
    path{
      fill: $red;
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media screen and (min-width: 993px){
    position: relative;
    z-index: 4;
  }
}

.header-lk{
  margin-right: 2.5em;
  &__btn{
    width: 1.5625em;
    @include center;
    @include lastM(0 .94em 0 0);
    svg{
      width: 100%;
    }
    &:hover{
      circle,
      path:not(._stroke){
        fill: $red;
      }
      ._stroke{
        stroke: $red;
      }
    }
  }
}

.header-search{
  position: relative;
  &._open{
    .header-search__toggle{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
    .header-search__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &._show-results{
    .search-result{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__btn,
  &__toggle{
    width: 1.3125em;
    height: 1.3125em;
    &:hover{
      path{
        fill: $red;
      }
    }
  }
  &__toggle{
    position: relative;
    svg{
      @include centerAbsolute;
      width: 100%;
    }
    ._view2{
      opacity: 0;
    }
  }
  &__dropdown{
    position: absolute;
    top: 100%;
    right: 0;
    margin-top: .5em;
    width: 13.5em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__field{
    display: flex;
    align-items: center;
  }
  &__btn{
    @include center;
    flex-shrink: 0;
    margin-right: .94em;
    svg{
      width: 100%;
    }
  }
  .fg{
    flex-grow: 1;
    input{
      width: 100%;
      font-size: .75em;
      border: none;
      border-bottom: 1px solid $black;
      height: 2.17em;
      padding: 0 .42em;
      color: $black;
      &:focus{
        border-color: $primary;
      }
      &::placeholder{
        color: rgba($black, 0.50);
      }
    }
  }
}

.search-result{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  padding: .625em 0;
  background-color: #fff;
  box-shadow: 0 .25em .625em 0 rgba(0, 0, 0, 0.10);
  @include grid100gap(.3125em);
  &__title,
  &__link{
    padding: 0 .625em;
    word-break: break-word;
  }
  &__items{
    @include grid100gap(.3125em);
  }
  .text11{
    letter-spacing: 0.1em;
  }
}

.search-card{
  padding: .625em;
  display: grid;
  grid-template-columns: 2.2em calc(100% - 3.14em);
  gap: .94em;
  align-items: center;
  &:hover{
    background-color: $black;
    color: #fff;
  }
  &__img{
    background-color: #fff;
    @include imgCover(130.4%);
  }
  &__content{
    @include grid100gap(.19em);
  }
}


.menu__bg,
.menu__close,
.burger{
  display: none;
}

.catalog-menu{
  width: 100%;
  @media screen and (min-width: 993px){
    position: absolute;
    top: 100%;
    left: 0;
    background: #F8F8F8;
    box-shadow: 0 .25em 1.875em 0 rgba(0, 0, 0, 0.10);
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    padding: 2.5em 0 1.875em;
    &._open{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 24em) 18em;
    gap: 6em;
    width: 58.75em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__links{
    display: flex;
    justify-content: space-between;
  }
}

.banner{
  position: relative;
  display: block;
  &:hover{
    color: $black;
  }
  &__img{
    display: block;
    width: 100%;
  }
  &__content{
    @include centerAbsolute;
    width: calc(100% - 5.2em);
    padding: .6em;
    text-align: center;
    text-transform: uppercase;
    @include grid100gap(.3125em);
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      background: rgba(255, 255, 255, 0.10);
      backdrop-filter: blur(1px);
    }
  }
  &__desc{
    letter-spacing: 0.00875em;
  }
}

.catalog-nav{
  &__title{
    text-transform: uppercase;
    font-weight: 600;
    margin-bottom: 1.5625em;
  }
  ul{
    @include grid100gap(.5em);
  }
  .menu-item{
    color: #616366;
    text-transform: uppercase;
    a{
      font-size: .6875em;
      line-height: 125%;
      letter-spacing: 0.00875em;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__inner{
      justify-content: space-between;
    }
    &__logo{
      margin: 0 10em;
    }
    &._open{
      .menu{
        transform: translateX(0);
        &__bg{
          display: block;
        }
        &__close{
          display: flex;
        }
      }
    }
  }

  .menu{
    @include menu;
    display: block;
  }

  .header-action{
    width: auto;
    flex-grow: 1;
  }

  .burger{
    @include center;
    width: 1.6em;
    img{
      width: 1.3125em;
      display: block;
    }
  }

  .catalog-menu{
    padding: .94em;
    display: none;
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
    &__links{
      @include grid100gap(1.875em);
    }
  }

  .catalog-nav{
    &__title{
      color: #444;
      margin-bottom: .94em;
    }
    ul{
      padding-left: .625em;
      gap: .94em;
    }
  }

  .catalog-btn span,
  .nav .menu-item a{
    color: #2B363B;
    text-transform: unset;
    font-size: .8125em;
  }

  .catalog-btn{
    padding: .9375em;
    justify-content: space-between;
    width: 100%;
    border-bottom: 1px solid #F5F5F5;
    &._open{
      svg{
        transform: rotate(-180deg);
      }
    }
  }

  .nav{
    ul{
      display: block;
    }
    .menu-item{
      border-bottom: 1px solid #F5F5F5;
      padding: .9375em;
      margin: 0 !important;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__inner{
      display: grid;
      grid-template-columns: calc(50% - 3.0625em) 3.125em calc(50% - 3.0625em);
      position: relative;
    }
    &__logo{
      margin: 0;
      width: 100%;
    }
  }

  .header-action{
    justify-content: flex-end;
    &__cart{
      white-space: nowrap;
      display: flex;
      align-items: center;
      &:before{
        content: '';
        background-image: url(../img/cart.svg);
        @include bgImgSize(100%);
        width: 0.96313em;
        height: 1.25em;
        flex-shrink: 0;
        margin-right: .13em;
      }
      &-title{
        display: none;
      }
    }
  }

  .header-lk{
    margin-right: .94em;
    &__link{
      width: 1.25em;
      height: 1.25em;
      background-image: url(../img/user.svg);
      @include bgImgSize(100%);
      span{
        display: none;
      }
    }
    &__btn{
      @include lastM(0 .625em 0 0);
    }
  }

  .header-search{
    position: absolute;
    top: 50%;
    left: 2.5em;
    transform: translateY(-50%);
    &__dropdown{
      right: unset;
      left: -2.5em;
      width: calc(100vw - 1.875em);
      margin-top: 1em;
    }
    &__field{
      position: relative;
    }
    &__btn{
      position: absolute;
      right: .12em;
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
    }
    .fg{
      input{
        height: 3.33em;
        border: .6px solid $black;
      }
    }
  }
}
