@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.5em;
    height: 3.375em;
    font-weight: 700;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
        line-height: 120%;
        letter-spacing: 0.0625em;
    }
}

.m-btn-gold{
    background-color: $primary;
    color: $black;
}

.m-btn-black{
    background-color: $black;
    color: #fff;
}

.m-btn-white{
    background-color: #fff;
    color: $black;
}

.m-btn-white,
.m-btn-gold,
.m-btn-black{
    &:active,
    &:hover{
        background-color: #666;
        color: #fff;
    }
    &:disabled{
        background-color: $grey;
        color: #fff;
        pointer-events: none;
    }
}
