.main-slider{
  overflow: hidden;
}

.main-slide{
  background-size: cover;
  background-position: center;
  padding: 8.44em 0;
  min-height: 45em;
  color: #fff;
  &__top{
    @include grid100gap(.83em);
  }
  &__subtitle{
    font-weight: 900;
    color: #D5AC6E;
    line-height: 165%;
    text-transform: uppercase;
    letter-spacing: 0.02em;
  }
  &__title{
    letter-spacing: 0.01em;
  }
  &__nav{
    margin-top: 11.5em;
    display: flex;
  }
}

.collection{
  padding: 3.75em 0 0;
  overflow: hidden;
  &__inner{
    position: relative;
  }
  &__top{
    max-width: 37.5em;
    @include grid100gap(1.19em);
    .text46,
    .text12{
      letter-spacing: 0.03125em;
    }
  }
  @media screen and (min-width: 577px){
    .swiper-nav{
      display: flex;
      align-items: center;
      position: absolute;
      right: 0;
      top: 0;
    }
    .swiper-button{
      position: static;
      margin: 0 0 0 1.25em;
    }
  }
}

.collection-slider{
  margin-top: 3.125em;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-slide{
      width: calc(100% / 3);
    }
  }
}

.product-card{
  padding-right: 2.5em;
  position: relative;
  &__img{
    @include imgCover(130.5%);
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 1.25em 2.5em 9.5em 3.125em;
    display: flex;
    flex-direction: column;
  }
  &__top{
    margin-left: -3.125em;
    text-transform: uppercase;
    font-weight: 600;
    height: 8.6em;
    .text36{
      letter-spacing: 0.02em;
    }
  }
  &__weight{
    padding: .625em 0;
    text-transform: uppercase;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    &:before{
      content: '';
      width: 0.625em;
      height: 0.0625em;
      flex-shrink: 0;
      margin: 0 0 .31em;
      display: block;
      background: $black;
    }
    span{
      font-size: .79em;
      line-height: 129%;
      letter-spacing: 0.0625em;
    }
  }
  &__bottom{
    padding-top: 1.25em;
    margin-top: auto;
    display: flex;
    flex-direction: column;
    min-height: 6.5em;
  }
  &__price{
    font-weight: 600;
    letter-spacing: 0.02rem;
  }
  &__sale{
    line-height: 136%;
    letter-spacing: 0.02em;
    font-weight: 600;
    color: #D0121A;
  }
  &__nav{
    margin-top: auto;
    display: flex;
    padding-top: 2em;
  }
  &__link{
    text-transform: uppercase;
    letter-spacing: 0.02rem;
    font-weight: 800;
    span{
      font-size: .72em;
      line-height: 163%;
    }
  }
}

.swiper-button{
  width: 2em;
  @include center;
  svg{
    width: 100%;
  }
  &:hover{
    path{
      stroke: $red;
    }
  }
}

.why-we{
  background: #FEFAF6;
  padding: 4.38em 0;
  &__title{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,25%);
    margin-top: 2.5em;
  }
}

.why-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__icon{
    height: 4em;
    display: block;
    flex-shrink: 0;
    margin-bottom: .625em;
  }
  &__text{
    font-size: 1.03em;
    line-height: 130%;
    font-style: Italic;
    letter-spacing: 0.02em;
  }
}


@media screen and (max-width: 992px){
  .main-slide{
    padding: 2.75em 0;
    min-height: 21.25em;
    &__nav{
      margin-top: 4.7em;
    }
  }

  .collection{
    padding-bottom: 5em;
  }

  .collection-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .product-card{
    padding-right: 0;
    &__content{
      padding-right: 1em;
      padding-left: 2.5em;
    }
    &__top{
      margin-left: -2.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .main-slide{
    padding: 1.75em 0;
    min-height: 21.875em;
    display: flex;
    flex-direction: column;
    color: $black;
    &__nav{
      margin-top: 1em;
      justify-content: flex-end;
      .m-btn{
        padding: 0 1.875em;
        height: 2.53em;
        span{
          font-size: .65em;
        }
      }
    }
    .container{
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }
    &__subtitle{
      display: none;
    }
    &__title{
      font-size: 1.5em;
    }
  }

  .collection{
    &__top{
      padding: 0;
    }
    .text46{
      max-width: 7.25em;
    }
  }

  .collection-slider{
    margin-top: 1.875em;
    position: relative;
    .swiper{
      &-container{
        transform: translateX(1.94em);
      }
      &-slide{
        width: 100%;
      }
      &-button{
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
    }
  }

  .product-card{
    &__content{
      padding-top: 1em;
      padding-bottom: 7.5em;
    }
    &__top{
      height: 6em;
      .text36{
        font-size: 1.75em;
      }
    }
    &__weight{
      margin-top: 1em;
      padding: .5em 0;
      span{
        font-size: .625em;
      }
    }
    &__price{
      font-size: 1.125em;
      letter-spacing: 0.01625em;
    }
    &__sale{
      letter-spacing: 0.00625em;
    }
    &__link{
      span{
        font-size: .625em;
      }
    }
  }

  .why-we{
    &__grid{
      grid-template-columns: repeat(2,50%);
      row-gap: 1.56em;
    }
  }
}
