.categories{
  padding: 6.25em 0;
  &__grid{
    margin-top: 4.38em;
    padding-bottom: 5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
  }
}

.category-card{
  position: relative;
  &__top{
    text-align: center;
    @include grid100gap(.5em);
  }
  &__title{
    text-transform: uppercase;
  }
  &__img{
    margin-top: 2.5em;
    @include imgCover(104%);
  }
  &__link{
    position: absolute;
    bottom: -1.25em;
    left: 50%;
    transform: translateX(-50%);
  }
}

.subscribe{
  padding: 3.6em 0;
  background: $black;
  color: #fff;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
    align-items: center;
  }
  &__info{
    @include grid100gap(.625em);
    .text14{
      line-height: 143%;
    }
  }
}

.subscribe-form{
  display: flex;
  align-items: center;
  &__field{
    flex-grow: 1;
    input{
      font-size: 1em;
      height: 3.125em;
      width: 100%;
      padding: 0 .625em;
      border-radius: .4375em;
      background-color: #fff;
      border: none;
      &:focus{
        border-color: $primary;
      }
      &::placeholder{
        color: rgba($black,.5);
      }
    }
  }
  &__btn{
    margin-left: .94em;
    height: 3.125em;
    flex-shrink: 0;
    font-weight: 700;
    text-transform: unset;
    border-radius: 0.4375em;
    span{
      font-size: .9375em;
    }
  }
}


@media screen and (max-width: 992px){
  .categories{
    padding: 3.125em 0 6.25em;
    &__grid{
      margin-top: 3.125em;
      padding-bottom: 0;
      grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      gap: 1.25em;
    }
  }

  .category-card{
    &__img{
      margin-top: 1.875em;
    }
    &__link{
      bottom: -1.56em;
    }
  }

  .subscribe{
    padding: 4em 0;
    &__inner{
      grid-template-columns: 19.25em calc(100% - 20.5em);
      gap: 1.25em;
    }
    &__info{
      gap: 1.25em;
    }
  }

  .subscribe-form{
    &__btn{
      padding: 0 .72em;
    }
  }
}


@media screen and (max-width: 576px){
  .categories{
    &__grid{
      grid-template-columns: 100%;
      gap: 5em;
    }
  }

  .subscribe{
    padding: 2em 0;
    text-align: center;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .subscribe-form{
    flex-direction: column;
    align-items: center;
    &__field{
      width: 100%;
    }
    &__btn{
      padding: 0 2.5em;
      margin: 1.25em auto 0;
    }
  }
}
