.footer{
  padding: 3.125em 0 1.5em;
  &__inner{
    min-height: 16.75em;
    display: grid;
    grid-template-columns: 9.75em 9.75em 9.75em calc(100% - 69.75em) 9.75em 9.75em 9.75em;
    gap: 1.875em;
    align-items: flex-start;
  }
  &__logo{
    display: block;
    width: 3.125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: 2.5em;
    text-align: center;
  }
  &__c{
    font-size: .75em;
    line-height: 133%;
    font-weight: 600;
  }
  @media screen and (min-width: 993px){
    &__col2{
      grid-column: span 2;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .9375em));
      align-items: flex-start;
      gap: 1.875em;
    }
    &__col3{
      display: flex;
      justify-content: center;
    }
    &__col4{
      grid-column: span 2;
    }
  }
}

.footer-nav{
  @include grid100gap(1.25em);
  &__title{
    padding-bottom: .3125em;
    position: relative;
    text-transform: uppercase;
    .text10{
      line-height: 170%;
      letter-spacing: 0.032em;
    }
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 1.25em;
      height: 0.0625em;
      background: $black;
    }
  }
  &__group{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    text-transform: uppercase;
    @include lastM(0 0 .5em);
    span{
      font-size: .625em;
      line-height: 140%;
      letter-spacing: 0.014em;
    }
  }
}

.footer-contacts{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__instagram{
    display: inline-flex;
    align-items: center;
    text-transform: uppercase;
    img{
      width: 1.9em;
      margin-right: .94em;
    }
    span{
      letter-spacing: 0.02em;
    }
    &-hint{
      padding: .3875em 0;
      span{
        font-size: 0.625em;
        line-height: 150%;
      }
    }
  }
  &__phone{
    margin-top: 1.25em;
    text-transform: uppercase;
    font-weight: 500;
    span{
      font-size: 1em;
      line-height: 188%;
    }
  }
  &__email{
    font-weight: 500;
    span{
      font-size: 1em;
      line-height: 138%;
    }
  }
  &__group{
    margin-top: 1.25em;
    &-title{
      margin-bottom: .19em;
      span{
        font-size: .625em;
        line-height: 140%;
        letter-spacing: 0.032em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .9375em));
    }
    &__col2{
      @include grid100gap(1.875em);
    }
    &__col3{
      order: 4;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 2.5em;
      position: relative;
      &:before{
        content: '';
        order: 2;
      }
    }
    &__col2{
      @include grid100gap(1.875em);
      order: 5;
    }
    &__col3{
      order: 1;
      grid-column: 1/-1;
      display: flex;
      justify-content: center;
    }
    &__col4{
      order: 3;
    }
    &__col1{
      order: 4;
    }
    ._mob-first{
      position: absolute;
      top: 5.4375em;
      left: 0;
      width: calc(50% - 1.25em);
    }
    &__logo{
      width: auto;
      img{
        height: 2.9375em;
        width: auto;
      }
    }
  }

  .footer-contacts{
    &__instagram{
      &-hint{
        max-width: 90%;
        padding: 0;
        margin-top: .31em;
        span{
          font-size: .5em;
          line-height: 110%;
        }
      }
    }
    &__phone{
      margin-top: .625em;
      span{
        font-size: .875em;
        line-height: 214%;
      }
    }
    &__group{
      &-title{
        span{
          font-size: .5em;
          line-height: 137%;
        }
      }
    }
    &__email{
      span{
        font-size: .875em;
        line-height: 136%;
      }
    }
  }
}
